@use "../settings" as *;
@use "../settings-options" as *;

// stylelint-disable declaration-no-important

// Overflow utilties
@if $enable-utility-overflow {
  @if $utility-overflow != null {
    @each $value in $utility-overflow {
      .overflow-#{$value} { overflow: $value !important; }
    }
  }
}

@if $enable-utility-overflow-x {
  @if $utility-overflow-x != null {
    @each $value in $utility-overflow-x {
      .overflow-x-#{$value} { overflow: $value !important; }
    }
  }
}

@if $enable-utility-overflow-y {
  @if $utility-overflow-y != null {
    @each $value in $utility-overflow-y {
      .overflow-y-#{$value} { overflow: $value !important; }
    }
  }
}
