@use "../settings-options" as *;

@if $enable-utility-stacks {
  @if $enable-utility-stacks-horizontal {
    .hstack {
      display: flex;
      flex-direction: row;
      align-items: center;
      align-self: stretch;
    }
  }
  @if $enable-utility-stacks-vertical {
    .vstack {
      display: flex;
      flex: 1 1 auto;
      flex-direction: column;
      align-self: stretch;
    }
  }
}
